.back {
    display: flex;
    justify-content: center;
    align-items: center;
}

.back1 {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    background-image: url("../assets/images/back.png");
    background-color: #b8e5f8;
    filter: blur(10px);
}

.register-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 432px;
    background-color: white;
}

.register-container h1 {
    margin: 10px;
    color: #0084ff;
    font-size: 60px;
}

.register-container h2 {
    margin-bottom: 20px;
    color: #0084ff;
    font-size: 20px;
}

.register-container .input {
    width: 300px;
}

.register-container .btn {
    margin-bottom: 30px;
    width: 300px;
    height: 40px;
    border-radius: 8px;
}

.register-container .login {
    margin: 0;
    padding: 20px 0;
    align-self: stretch;
    text-align: center;
    font-size: 16px;
    background-color: #f6f6f6;
}